.relativitionblock {
  position: relative;
  transition: all .5s ease;
  cursor: pointer; }

.checkbox {
  position: absolute;
  top: 5px;
  right: 25px;
  display: none; }

.checkbox input[type="checkbox"] {
  display: none; }

.checkbox {
  display: none; }

.checkbox label::before {
  content: "";
  background: #00B5FE;
  border-radius: 2px;
  display: inline-block;
  height: 20px;
  width: 20px;
  cursor: pointer; }

.checkbox label::after {
  content: "";
  margin-top: -14px;
  margin-left: -1px;
  cursor: pointer; }

.checkbox label {
  position: relative; }

.checkbox label::before, .checkbox label::after {
  position: absolute; }

/*Внешний блок*/
.checkbox label::before {
  top: 3px; }

/*Галка*/
.checkbox label::after {
  left: 5px;
  top: 20.5px;
  background-size: 10px 5px; }

/*Прячем галку по умолчанию*/
.checkbox input[type="checkbox"] + label::after {
  content: none; }

/*Показываем галку по состоянию checked*/
.checkbox input[type="checkbox"]:checked + label::after {
  content: url("./img/tick.png"); }

.togglerclassser {
  transform: scale(1.1);
  transition: all .5s ease; }

.togglerclassser img {
